// import ap from '../../assets/js/ap'

export default {
    
    data(){
        return{
        appointId:'',
        name:'',
        phone:'',
        appointQuantity:'',
        subject:'',
        total_amount:''
        }   
    },
    mounted(){
        //加载下单时间的参数
        this.getOrderParms()
    },
    methods:{
        getOrderParms(){
            this.appointId = this.$route.query.appointId
            this.name = this.$route.query.name
            this.phone= this.$route.query.phone
            this.appointQuantity = this.$route.query.appointQuantity
            this.subject = this.$route.query.subject
            this.total_amount = this.$route.query.total_amount
            
        },
        isPay(){
     
               this.$router.push({path:'/alipay',
               query:{
             openid:getCookie('openid'),          
            // openid:'oA9nMwK9OluRANG86pHl8YeCERAk',
             appointId: this.appointId,
             name: this.name,
             phone: this.phone,
             appointQuantity:this.appointQuantity,
             subject: this.subject,
             total_amount: this.total_amount}
               })
        }
    }


}

     





