
import NavBar from './components/NavBar'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
name:'customer',
components:{
XNumber,
Group,
Cell,
InlineXNumber,
XInput,
NavBar,
Scroller
},
data(){
return{
roundValue: 0,
dateTime:'',
serviceName:'',
money:'',
weekDate:'',
totalPrice:'',
username:'',
phone:'',
appointId:'',
docmHeight: '0',  //默认屏幕高度
showHeight:  '0',  //实时屏幕高度
hideshow:true,  //显示或者隐藏footer,
isResize:false, //默认屏幕高度是否已获取
calendarWeek:''  

}
},
watch:{
roundValue(newName,oldName){
console.log('滚动',newName)
this.totalPrice = newName * this.money
},











},
created(){

},
mounted(){

this.getToCustomer();

this.showWeek();
















},
methods:{

getToCustomer(){
var time = this.$route.query.dateTime;
var name = this.$route.query.servieName;
var yuan = this.$route.query.money;
var date = this.$route.query.date;
var id   = this.$route.query.appointId;
console.log("customer----->>>接受过来的传输",time,name,yuan,date,id)
this.dateTime = time;
this.serviceName = name;
this.money = yuan;
this.weekDate = date;
this.appointId = id;





},

getHeight(){



console.log("默认屏幕高度:"+document.documentElement.clientHeight)
console.log("实时屏幕高度:"+document.body.clientHeight)
this.buttomHeight = this.$refs.buttomOrder.$el.offsetTop  
console.log("buttomHeight",this.buttomHeight)
console.log("docmHeight:",this.docmHeight)
console.log("showHeight:",this.showHeight)
},
lostfocus(){
console.log("失去焦点")
if(this.hideshow == false){
this.hideshow = true
}
},
getFocus(){
console.log("获取焦点")
if(this.hideshow == true){
this.hideshow = false
}
},
changeNum(val){
this.roundValue = val;
console.log('changeNum',this.roundValue)
},


showWeek(){
var str = this.$route.query.date;
console.log("customer 日期："+str)

var calendarObject = new Date(str)
var day = calendarObject.getDay()
if(day == 1){
this.calendarWeek = '星期一';
console.log('星期一',day);
}
if(day == 2){
this.calendarWeek = '星期二';
console.log('星期二',day);
}
if(day == 3){

this.calendarWeek = '星期三';
console.log('星期三',day);
}
if(day == 4){
this.calendarWeek = '星期四';
console.log('星期四',day);
}
if(day == 5){
this.calendarWeek = '星期五';
console.log('星期五',day);
}
if(day == 6){
this.calendarWeek = '星期六';
console.log('星期六',day);
}
if(day == 0){
this.calendarWeek = '星期天';
}
}
}
}




