
import Confirm from 'vux/src/components/confirm/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import XButton from 'vux/src/components/x-button/index.vue'

import Alert from 'vux/src/components/alert/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Scroller from 'vux/src/components/scroller/index.vue'

import axios from 'axios'
import HomeBottom from '../home/components/bottom'
export default {
directives: {
TransferDom
},
name:'orderstatus',
components:{
XButton,
HomeBottom,
Confirm,
ConfirmPlugin,
Alert,
AlertModule,
Scroller
},
data(){
return{
orderDetail:[],
show: false,
orderId:'',
currentIndex:0,
statusHeight:'',
}
},
created(){

this.getByOrderDetail()
},
mounted(){

setTimeout(function(){
var obj = document.getElementsByClassName("order-have-in")[0];
this.statusHeight = (obj.offsetHeight+50)+'px';
console.log("高度为："+this.statusHeight);
},1000);









},

beforeDestroy () {
clearInterval(this.timer)
},

methods:{
toHome(){
this.$router.push({path:'/'})
},
getByOrderDetail(){

axios({
method: 'get',
url: 'http://www.gouptea.top/haircut/customer/order/api/orderDetail',
params: {
'openid':getCookie('openid')


}
}).then((response)=>{
console.log(response)
this.orderDetail = response.data.data

})
},


cancelOrder(){
axios({
method: 'post',
url: 'http://www.gouptea.top/haircut/customer/order/api/cancel',
params: {
'openid':getCookie('openid'),

'orderId':this.orderId
}
}).then((response)=>{
console.log(response)

this.showModule()

this.$router.push({path:'/'})
})
},



onCancel () {
console.log('on cancel')
},
onConfirm (msg) {
console.log('on confirm')
if (msg) {
alert(msg)
}
if(this.show){
console.log("确定下单")


this.orderId = this.$refs.cancelOrderId[this.currentIndex].value
console.log("退约orderId = ",this.orderId)
this.cancelOrder()
}
},onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},

isReturnTicket(index){
console.log("退约....")
this.show = true
this.currentIndex = index
},

showModule () {
AlertModule.show({
title: '消息',
content: '恭喜您,退约已成功!',
onShow () {
console.log('Module: I\'m showing')
},
onHide () {
console.log('Module: I\'m hiding now')
}
})
},

showModuleAuto () {
this.showModule()
setTimeout(() => {
AlertModule.hide()
}, 3000)
},
}
}

function getCookie(name) {
var arr;
var reg = new RegExp('(^| )' +name+"=([^;]*)(;|$)");
if(arr=document.cookie.match(reg))
return unescape(arr[2]);
else
return null;
}



