import axios from 'axios'
export default {
    //接受父子组件传过来的值
    props:{
        openid:{
            type:String
        }
    },
    data(){
        return{
            order:{}
        }
    },
    created(){
        // alert(this.openid)
        this.getByOpenid()
    },
    methods:{
        getByOpenid(){
               if(this.openid != null){
            axios({
                method: 'get',
                url:'http://www.gouptea.top/haircut/order/userInfo/api/getByOrderyOpenId',
                params: {
                    openid:this.openid
                    // openid:'oA9nMwK9OluRANG86pHl8YeCERAk'
                }
            }).then((res)=>{
                console.log(res.data.data);
                this.order = res.data.data
            })    
               }
        }
    }
}



