
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


const baseList = [{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: '送你一辆车'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg', // 404
title: '送你一次旅行',
fallbackImg: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw50iwj20ff0aaaci.jpg'
}]

const imgList = [
'http://placeholder.qiniudn.com/800x300/FF3B3B/ffffff',
'http://placeholder.qiniudn.com/800x300/FFEF7D/ffffff',
'http://placeholder.qiniudn.com/800x300/8AEEB1/ffffff'
]

const urlList = baseList.map((item, index) => ({
url: 'http://m.baidu.com',
img: item.img,
fallbackImg: item.fallbackImg,
title: `(可点击)${item.title}`
}))

const demoList = imgList.map((one, index) => ({
url: 'javascript:',
img: one
}))

const only2ClickList = baseList.slice(0, 2).map(item => {
item.url = 'http://m.baidu.com'
return item
})

export default {
components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider
},
ready () {

},
methods: {
onSwiperItemIndexChange (index) {
console.log('demo item change', index)
},
demo01_onIndexChange (index) {
this.demo01_index = index
},
demo05_onIndexChange (index) {
this.demo05_index = index
},
demo05_onLoad (id) {
this.demo05_list = id === 1 ? baseList : demoList
},
demo06_onIndexChange (index) {
this.demo06_index = index
},
demo07_onIndexChange (index) {
this.demo07_index = index
}
},
data () {
return {
demo01_list: baseList,
demo02_list: demoList,
demo03_list: demoList,
demo04_list: imgList,
demo05_list: [],
demo06_list: urlList,
demo07_list: only2ClickList,
demo01_index: 0,
demo02_index: 1,
demo05_index: 0,
demo06_index: 0,
demo07_index: 0,
swiperItemIndex: 1
}
}
}



