
import TobeConsumed from '../order/components/tobeconsumed'
import OrderConsumed from '../order/components/consumed'
import OrderRefund from '../order/components/refund'
import axios from 'axios'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

import { log } from 'util'
const list = () => ['待支付', '已支付', '已退款']
export default {
directives: {
TransferDom
},
components:{
Tab,
TabItem,
TobeConsumed,
OrderConsumed,
OrderRefund,
Swiper,
SwiperItem,
Loading,
Scroller
},
data(){
return{

list2: list(),
demo2:'待支付',
index:0,
currenIndex:0,
orderDetailList: [],
num:0,

}
},
created(){

this.showLoading()
setTimeout(() => {
this.getOrderDetail();
}, 800)
},
mounted(){


},
watch:{







},
methods:{

showLoading () {
this.$vux.loading.show({
text: ''
})
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
},

updateHeight(e){
let topHeight = document.getElementsByClassName('tab-swiper')[0].offsetHeight,
diffHeight = window.screen.height-topHeight - 55 ;
this.$nextTick(()=>{
this.$refs.cateSwiper.xheight = diffHeight + 'px'
})
},
handler (index) {
console.log("index:"+index)

},
next(){
if(this.currenIndex === this.list2.length - 1){
this.currenIndex = 0
}else{
++this.currenIndex
}
console.log("currenIndex",this.currenIndex)
},


getOrderDetail(){
axios({
method: 'get',
url:'http://www.gouptea.top/haircut/customer/order/api/list',

params: {
'openid': getCookie('openid')

}
}).then((res)=>{
this.respone = res.data.data

this.orderDetailList = this.respone;






})
}
}
}
function getCookie(name) {
var arr;
var reg = new RegExp('(^| )' +name+"=([^;]*)(;|$)");
if(arr=document.cookie.match(reg))
return unescape(arr[2]);
else
return null;
}



