
import Scroller from 'vux/src/components/scroller/index.vue'

import Alert from 'vux/src/components/alert/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import XButton from 'vux/src/components/x-button/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'

export default {

props:['aName'],
components:{
Scroller,
XButton,
Calendar
},
data(){
return{
demo2: 'TODAY',
calendarWeek:'',
}
},
created(){

},
mounted(){

},
watch:{

},
methods:{
toList(){

var selectDate  = this.$refs.calendarValue.value;
var aName = this.$refs.serviceName.innerHTML;

if((aName.trim()) == '请输入需要的服务'){
this.showModule()
return;
}
console.log("selectDate = ",selectDate);
console.log("home_name = ",aName);
this.$router.push({path:'/listed',query:{selectDateTime:selectDate,appointname:aName}})
},

change(val){

var calendarValue  = val;
var calendarObject = new Date(calendarValue);
var day = calendarObject.getDay();

var dateObject = new Date();



var year = dateObject.getFullYear();
var month = dateObject.getMonth()+1;
var date = dateObject.getDate();
var sysCalendar = year +'-' + month + '-' + date;


console.log(sysCalendar+ '  ===== ' + val);
if(val == sysCalendar){
this.calendarWeek = '今天';
console.log('今天',day);
return;
}else{

if(day == 1){
this.calendarWeek = '星期一';
console.log('星期一',day);
}
if(day == 2){
this.calendarWeek = '星期二';
console.log('星期二',day);
}
if(day == 3){

this.calendarWeek = '星期三';
console.log('星期三',day);
}
if(day == 4){
this.calendarWeek = '星期四';
console.log('星期四',day);
}
if(day == 5){
this.calendarWeek = '星期五';
console.log('星期五',day);
}
if(day == 6){
this.calendarWeek = '星期六';
console.log('星期六',day);
}
if(day == 0){
this.calendarWeek = '星期天';
}
}

},

showModule () {
AlertModule.show({
title: '消息',
content: '请您输入需要的服务',
onShow () {
console.log('Module: I\'m showing')
},
onHide () {
console.log('Module: I\'m hiding now')
}
})
}
},

showModuleAuto () {
this.showModule()
setTimeout(() => {
AlertModule.hide()
}, 3000)
},
}



